import React, { useState, useEffect } from 'react';
import { ThemeProvider, createTheme, CssBaseline } from '@mui/material';
import Navbar from './components/Navbar';
import TrailTable from './components/TrailTable';
import Footer from './components/Footer';
import axios from 'axios';

function App() {
  const [darkMode, setDarkMode] = useState(false);
  const [trails, setTrails] = useState([]);
  const [openStatus, setOpenStatus] = useState([]); //  openStatus 

  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
    },
  });

  useEffect(() => {
    fetchTrails();
    fetchOpen();
  }, []);

  const fetchTrails = async () => {
    try {
      const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ForestRtBasic.aspx');
      const trails = response.data.map(trail => ({
        //  API 
        "": trail.TR_CNAME, 
        "": trail.TR_POSITION,
        "": trail.TR_DIF_CLASS, 
        "": trail.TR_PAVE,
        "": trail.TR_LENGTH,
        "": trail.TR_ALT,
        "": trail.TR_BEST_SEASON,
        // ...
      }));
      setTrails(trails);
    } catch (error) {
      console.error('Error fetching trails:', error);
    }
  };

  const fetchOpen = async () => {
    try {
      const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ForestRtOpen.aspx');
      const open = response.data.map(open => ({
        //  API 
        "": open.TRAILID,
        "": open.TR_CNAME, 
        "": open.TR_TYP,
        "": open.Title, 
        "": open.content,
        "": open.ANN_DATE,
        "": open.DEP_NAME,
      }));
      setOpenStatus(open); //  setOpenStatus 
    } catch (error) {
      console.error('Error fetching trails OPEN :', error);
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Navbar darkMode={darkMode} setDarkMode={setDarkMode} />
      <TrailTable trails={trails} openStatus={openStatus} /> {/*  openStatus  TrailTable */}
      <Footer />
    </ThemeProvider>
  );
}

export default App;
