import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper,
  TextField, Button, Tooltip
} from '@mui/material';
import { styled } from '@mui/material/styles';


const OuterContainer = styled('div')({
  display: 'flex', //  flexbox 
  justifyContent: 'center', // 
  width: '100%', // 
});


const TableWrapper = styled('div')({
  width: '80%',
  maxWidth: '1220px',
  '& .MuiTableContainer-root': {
    maxHeight: '80vh', // 
    overflowY: 'auto', 
  },
});

const SearchContainer = styled('div')(({ theme }) => ({
  display: 'flex',
  justifyContent: 'center',
  margin: '20px 0',
}));

const SearchInput = styled(TextField)(({ theme }) => ({
  marginRight: '10px',
}));

const CustomTableContainer = styled(TableContainer)(({ theme }) => ({
  maxHeight: 440,
}));

const ClearButton = styled(Button)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'light' ? '#3B7A57' : '#E0F2F1', // light mode  navbar dark mode 
  '&:hover': {
    backgroundColor: theme.palette.mode === 'light' ? '#2E6349' : '#CCE8E7', // hover 
  },
}));

function TrailTable({ trails }) {
  const [searchTerm, setSearchTerm] = useState('');
  const [sortColumn, setSortColumn] = useState('');
  const [sortDirection, setSortDirection] = useState('asc');
  const [openStatus, setOpenStatus] = useState([]);

  useEffect(() => {
    const fetchOpenStatus = async () => {
      try {
        const response = await axios.get('/api/open-status');
        setOpenStatus(response.data);
      } catch (error) {
        console.error('Error fetching open status:', error);
      }
    };
    fetchOpenStatus();
  }, []);
  
  const handleSort = (column) => {
    if (sortColumn === column) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortColumn(column);
      setSortDirection('asc');    
    }
  };

  const filteredTrails = trails.filter(trail =>
    Object.values(trail).some(value =>
      value.toString().toLowerCase().includes(searchTerm.toLowerCase())
    )
  );

  const sortedTrails = [...filteredTrails].sort((a, b) => {
    if (a[sortColumn] < b[sortColumn]) return sortDirection === 'asc' ? -1 : 1;
    if (a[sortColumn] > b[sortColumn]) return sortDirection === 'asc' ? 1 : -1;
    return 0;
  });

  const highlightText = (text, highlight) => {
    const safeText = text ? text.toString() : '';
    const parts = safeText.split(new RegExp(`(${highlight})`, 'gi'));
    return parts.map((part, index) => 
      part.toLowerCase() === highlight.toLowerCase() ? 
        <mark key={index}>{part}</mark> : part
    );  
  };

  // 
  const trailsWithStatus = sortedTrails.map(trail => {
    const status = openStatus.find(open => open[''] === trail['']);
    return {
      ...trail,
      '': status ? status[''] : '',
      '': status ? status[''] : '',
    };
  });

  return (
    <OuterContainer>
      <TableWrapper>
        <SearchContainer>
          <SearchInput
            label=" "
            variant="outlined"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <ClearButton variant="contained" color="primary" onClick={() => setSearchTerm('')}>
            
          </ClearButton>
        </SearchContainer>
        <CustomTableContainer component={Paper}>
        <Table stickyHeader>
            <TableHead>
              <TableRow>
                {['', '', '', '', '', '', ''].map((column) => (
                  <TableCell key={column} onClick={() => handleSort(column)}>
                    {column} {sortColumn === column && (sortDirection === 'asc' ? '' : '')}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {trailsWithStatus.map((trail) => (
                <TableRow key={trail['']}>
                  <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>
                  <Tooltip title={`: ${trail['']}`}>
                    <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>
                  </Tooltip>
                  <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>
                  <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>
                  <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>
                  <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>
                  <TableCell>{highlightText(trail[''], searchTerm)}</TableCell>

                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CustomTableContainer>
      </TableWrapper>
    </OuterContainer>
  );
}

export default TrailTable;
